<template>
  <div class="company-introduce-container">
    <div class="main-card">
      <div class="main-header">
        <span class="main-title">信息简报</span>
        <div class="close-btn" @click="cancelBtn">×</div>
      </div>

      <div class="contentBox">
        <div class="tab">
          <!-- <CloseBtn routeName="Home" /> -->
          <input type="text" class="searchStl" v-model="searchIpt" placeholder="请输入信息简报关键字搜索">
          <i class="el-icon-search iconStl" @click="requestTableData"></i>

        </div>

        <div class="table_box">
          <vue-seamless-scroll :data="tableData" class="seamless-warp" :class-option="classOption">
            <div class="table_body" v-for="(item,index) in tableData" :key="index">
              <span style="width: 200px">
                <!-- <span style="display: inline-block; width: 15px; height: 15px; background-color: skyblue; border-radius: 20px"></span> -->
              </span>
              <span style="width: 1399px">
                <a :href="item.website" target="_blank">{{item.title}}</a>
              </span>
              <span style="width: 200px">{{item.create_time}}</span>
            </div>
          </vue-seamless-scroll>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { http } from "@/utils/request";
import BottomLine from "@/components/BottomLine";
import Breadcrumb from "@/components/Breadcrumb";
import CloseBtn from "@/components/CloseBtn";
export default {
  data() {
    return {
      tableData: [],
      searchIpt: "",
    };
  },
  mounted() {
    this.requestTableData();
  },
  computed: {
    classOption() {
      return {
        step: 0.5, // 数值越大速度滚动越快
        // limitMoveNum: 10, // 开始无缝滚动的数据量 this.dataList.length
        hoverStop: true, // 是否开启鼠标悬停stop
        direction: 1, // 0向下 1向上 2向左 3向右
        // openWatch: true, // 开启数据实时监控刷新dom
        singleHeight: 0, // 单步运动停止的高度(默认值0是无缝不停止的滚动) direction => 0/1
        singleWidth: 0, // 单步运动停止的宽度(默认值0是无缝不停止的滚动) direction => 2/3
        waitTime: 1000, // 单步运动停止的时间(默认值1000ms)
      };
    },
  },
  methods: {
    cancelBtn() {
      this.$router.push({ name: "Home" });
    },
    requestTableData() {
      let params = {
        title: this.searchIpt,
      };
      http
        .get("/information_briefing/informationBriefingQueryPage", { params })
        .then((res) => {
          this.tableData = res.data.result;
          this.tableData.forEach((item) => {
            item.create_time = item.create_time.slice(0, 10);
          });
        });
    },
  },
  components: {
    Breadcrumb,
    CloseBtn,
    BottomLine,
  },
};
</script>

<style lang="scss" scoped>
input::-webkit-input-placeholder {
  /* placeholder颜色  */
  color: #fff;
  /* placeholder字号  */
  font-size: 0.14rem;
}
input::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #fff;
  font-size: 0.14rem;
}
input:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #fff;
  font-size: 0.14rem;
}
input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: #fff;
  font-size: 0.14rem;
}
.company-introduce-container {
  width: 2054px;
  height: 969px;
  .main-header {
    position: relative;
  }
  .contentBox {
    position: relative;
    margin-top: 80px;
    width: 1919px;
    height: 730px;
    .tab {
      position: relative;
      height: 80px;
      .tab-btn {
        font-size: 26px;
        line-height: 29px;
        display: inline-block;
        padding: 0 25px 20px;
        border-bottom: 2px solid #439799;
        &.active {
          color: #439799;
        }
      }
      .searchStl {
        width: 500px;
        height: 50px;
        padding-left: 20px;
        position: absolute;
        top: 0px;
        right: 200px;
        background-color: #041b3b;
        border-radius: 30px;
        border: 1px solid #0264ac;
        color: #fff;
      }
      .iconStl {
        position: absolute;
        top: 20px;
        right: 230px;
        font-size: 20px;
        cursor: pointer;
      }
    }
    .table_box {
      padding: 20px 40px;
      margin-top: 5px;
      overflow: hidden;
      width: 1850px;
      height: 625px;
      background: url("../../assets/dbsdmfx-bg.png") 0 no-repeat;
      background-size: 100% 100%;
      .table_body {
        height: 80px;
        line-height: 90px;
        //  border-bottom: 1px solid #439799;
        background: url("../../assets/xxzy/xxzy-noactive-bg.png") left center
          no-repeat;
        background-size: 100% 105%;
        margin-top: 20px;
        span {
          display: inline-block;
          font-size: 26px;
          color: #fff;
          // height: 65px;
          // line-height: 65px;
          // text-align: center;
        }
      }
      .table_body:hover {
        background: url("../../assets/xxzy/xxzy-active-bg.png") left center
          no-repeat;
        background-size: 100% 105%;
      }
    }
  }
}
</style>